<template>
  <div class="px-5" id="page-task-edit">
    <div class="scroll-to-container"></div>
    <b-card
      title=""
      class="mb-2 edit-task-container"
      ref="edit-task"
      id="edit-task"
      hide-footer
      v-if="creating || editing"
    >
      <div>
        <b-form class="mt-8" @submit="submitTask($event)">
          <b-form-group id="input-group-todo_task_id" label="ID" label-for="input-todo_task_id">
            <b-form-input
              id="input-todo_task_id"
              v-model="form.todo_task_id"
              type="text"
              required
              disabled
            ></b-form-input>
          </b-form-group>

          <b-form-group id="input-group-6" label="Typ av tgrd" label-for="input-6">
            <div class="radio-list mt-4">
              <template v-for="(type, j) in types">
                <label class="radio radio-outline radio-success mb-4" v-bind:key="j">
                  <input type="radio" v-model="form.type" :value="type.id" name="type" />
                  <span></span>
                  {{ type.name }}
                </label>
              </template>
            </div>
          </b-form-group>

          <div v-if="form.type === 'FILE_WIZARD'">
            <b-form-group id="input-group-7" label="Tilltna filtyper" class="mb-4 mt-8">
              <div class="check-list mt-4">
                <template v-for="(type, j) in filetypes_options">
                  <div class="d-flex align-items-center mb-4" v-bind:key="j">
                    <label
                      class="checkbox checkbox-lg checkbox-outline checkbox-success"
                    >
                      <input
                        type="checkbox"
                        name="filetypes"
                        v-model="filetypes_options[j].value"
                      />
                      <span></span>
                    </label>
                    <span class="ml-3 cursor-pointer">{{ type.text }}</span>
                  </div>
                </template>
              </div>
            </b-form-group>
          </div>

          <!--<div v-if="form.type === 'FILE_WIZARD'">
            <b-form-group label="Filkategori" label-for="filecategory" class="mt-8">
              <b-form-select
                id="new-search-period"
                v-model="form.category"
                :options="categoryOptions"
                text-field="name"
                value-field="id"
              >
              </b-form-select>
            </b-form-group>
          </div>-->

          <b-form-group id="input-group-1" label="Namn p tgrden" label-for="input-1">
            <b-form-input id="input-1" v-model="form.name" type="text"></b-form-input>
          </b-form-group>

          <b-form-group id="input-group-4" description="Var tydlig s att era underfreningar inte missfrstr" label="Beskrivning av tgrd" label-for="input-4">
            <b-textarea id="input-4" v-model="form.descr" class="mb-2"> </b-textarea>
          </b-form-group>

          <b-button type="submit" variant="primary">Spara tgrd</b-button>
          <b-button type="button" class="ml-4" variant="secondary" @click="closeTask">Stng</b-button>
        </b-form>

      </div>
    </b-card>
  </div>
</template>

<style lang="scss" scoped>
.checkbox.checkbox-light-success > span {
  background-color: white;
  border: 1px solid #ddd;
}
</style>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';

export default {
  name: 'taskedit',
  props: ['todo', 'tasks'],
  components: {},
  computed: {
    ...mapGetters(['currentCompanyId'])
  },
  mounted() {

  },
  data() {
    return {
      types: [
        { id: 'FILE_WIZARD', name: 'Ladda upp fil' },
        /*{ id: 'IMPORT_MEMBERS', name: 'Importera medlemmar' },*/
        { id: 'QUESTION', name: 'Stll frga' }
      ],
      filetypes: [],
      filetypes_options: [
        { id: 'pdf', text: 'PDF', value: false },
        { id: 'doc', text: 'Word', value: false },
        { id: 'xlsx', text: 'Excel', value: false },
        { id: 'zip', text: 'Zip', value: false },
        { id: 'jpg', text: 'JPG', value: false },
        { id: 'png', text: 'PNG', value: false },
      ],
      CATEGORIES: {
        FILE_EKONOMISKREDOVISNING: 'Ekonomisk redovisning',
        FILE_STADGAR: 'Stadgar',
        FILE_VERKSAMHET: 'Verksamhetsberttelse',
        FILE_ARSMOTESPROTOKOLL: 'rsmtesprotokoll',
        FILE_REV_PROT_MEDLEM: 'Revisorsprotokoll ver medlemmar',
        FILE_REV_PROT_EKONOMI: 'Revisorsprotokoll ver ekonomi',
        FILE_REV_REPORT: 'Revisorsrapport',
        FILE_OTHER: 'vriga filer'
      },
      categoryOptions: [
        { id: 'FILE_EKONOMISKREDOVISNING', name: 'Ekonomisk redovisning' },
        { id: 'FILE_STADGAR', name: 'Stadgar' },
        { id: 'FILE_VERKSAMHET', name: 'Verksamhetsberttelse' },
        { id: 'FILE_ARSMOTESPROTOKOLL', name: 'rsmtesprotokoll' },
        { id: 'FILE_REV_PROT_MEDLEM', name: 'Revisorsprotokoll ver medlemmar' },
        { id: 'FILE_REV_PROT_EKONOMI', name: 'Revisorsprotokoll ver ekonomi' },
        { id: 'FILE_REV_REPORT', name: 'Revisorsrapport' },
        { id: 'FILE_OTHER', name: 'vriga filer' }
      ],
      TODO_STATUS: {
        WAITING:    'WAIT',
        DRAFT:      'DRAFT',
        NOT_DONE:   'NOT',
        DONE:       'DONE',
        CONFIRMED:  'CN',
        DELETED:    'DEL',
        ACCEPTED:   'ACC',
        REJECTED:   'REJ',
        BACKLOG:    'BAC',
        FUTURE:     'FUT',
        INVALID:    'INVALID',
        CLOSE:      'CLOSE'
      },
      form: {
        todo_task_id: '',
        todo_id: '',
        name: '',
        descr: '',
        is_file: 0,
        status: 'DRAFT',
        type: '',
        category: '',
        files: [],
        valid_filetypes: [],
        valid_filtypes: [],
        comments: []
      },
      loaded: false,
      creating: false,
      editing: false,
      send_email: false
    };
  },
  watch: {
    currentCompanyId(newValue, oldvalue) {
      if (newValue) {

      }
    },
  },
  methods: {
    closeTask() {
      this.creating = false;
      this.editing = false;
    },
    async loadData() {
      if (!this.currentCompanyId) {
        return;
      }
      if (!this.titles) {
        return;
      }

    },
    submitTask(e) {
      e.preventDefault();

      this.updateTask();
    },
    updateTask() {
      this.form.valid_filetypes = this.filetypes_options.filter(item => item.value).map(item => item.id);
      this.form.todo_id = this.todo.todo_id;
      if (this.creating) {
        axios
          .post('/todo_task', this.form)
          .then(res => {
            if (res.status === 201) {
              this.creating = false;
              this.form = res.data.task;
              this.toastr('success', 'OK', 'tgrden skapades');
              this.closeTask();
              this.$emit('taskUpdated', this.form.todo_task_id);
            }
          })
          .catch(err => {
            console.error(err);
            this.toastr('danger', 'Server Fel', 'Kunde inte skapa rendet');
          });
      } else {
        axios
          .put('/todo_task', this.form)
          .then(res => {
            if (res.status === 200) {
              this.creating = false;
              this.form = res.data.task;
              this.toastr('success', 'OK', 'rendet uppdaterades');

              this.$emit('taskUpdated', this.form.todo_task_id);
            }
          })
          .catch(err => {
            console.error(err);
            this.toastr('danger', 'Server Fel', 'Kunde inte uppdatera rendet');
          });
      }
    },
    createTaskClicked() {
      this.form = {
        todo_task_id: '',
        todo_id: '',
        name: '',
        descr: '',
        is_file: 0,
        status: 'DRAFT',
        type: '',
        category: '',
        files: [],
        valid_filetypes: [],
        valid_filtypes:[],
        comments: []
      };

      this.createTask();
    },
    createTask() {
      this.creating = true;
      this.editing = true;

      this.scrollToEditTask();
    },
    selectTask(todo_task_id) {

      for (var i = 0; i < this.tasks.length; ++i) {
        if (this.tasks[i].todo_task_id === todo_task_id) {
          this.form = this.tasks[i];
        }
      }
        this.filetypes_options.filter((item, index) => {
          const valid = this.form.valid_filtypes.includes(item.id);
          this.filetypes_options[index].value = valid;
        });

      this.creating = false;
      this.editing = true;

      this.scrollToEditTask();
    },
    getLangText(lang_text, lang) {
      for (var i = 0; i < lang_text.length; ++i) {
        if (lang_text[i].lang === lang) {
          return lang_text[i].text;
        }
      }

      return 'Sprkversttning saknas';
    },
    scrollToEditTask() {
      var that = this;
      setTimeout(function () {
        const el = that.$el.getElementsByClassName('scroll-to-container')[0];

        if (el) {
          const yOffset = -70;
          const y = el.getBoundingClientRect().top + window.pageYOffset + yOffset;

          window.scrollTo({ top: y, behavior: 'smooth' });
        }
      }, 100);
    },
    toastr(type, title, message) {
      this.$bvToast.toast(message, {
        title: title,
        autoHideDelay: 2000,
        appendToast: true,
        variant: type
      });
    }
  }
};
</script>
